import React from 'react';
import contact from "../images/contact.png";
import contact1 from "../images/contact1.png"

function Contact() {
    return (
        <div className='container' style={{ marginTop: "50px" }}>
            <div className="row" >
                <div className='col-6'><img style={{ width: "500px" }} src={contact} alt="contact" /></div>
                <div className='col-6'><img style={{ width: "500px" }} src={contact1} alt="contact" /></div>
            </div >

        </div >

    )
}

export default Contact