import React from 'react';
import { Navbar, Container, Nav, NavDropdown } from 'react-bootstrap';
import logo from "../images/logo.png"
function NavBar() {
    return (
        <Navbar bg="light" variant="light">
            <Container>
                <Navbar.Brand href="/"> <img src={logo} style={{ width: "100px" }} alt="logo"></img><p style={{ marginBottom: 0 }}>THE SHARP MOTORS</p></Navbar.Brand>
                <Nav className="ms-auto">
                    <Nav.Link style={{ marginRight: "50px" }} href="/">Home</Nav.Link>
                    <NavDropdown style={{ marginRight: "50px" }} title="서비스" id="basic-nav-dropdown">
                        <NavDropdown.Item href="#action/3.1">정비</NavDropdown.Item>
                        <NavDropdown.Item href="#action/3.2">판금 도색</NavDropdown.Item>
                        <NavDropdown.Item href="#action/3.3">자동차 정기검사</NavDropdown.Item>
                    </NavDropdown>
                    <Nav.Link style={{ marginRight: "50px" }} href="/location">위치</Nav.Link>

                </Nav>
            </Container>
        </Navbar>
    )
}

export default NavBar