import React from 'react';
import customer from "../images/customer.jpg"

function Contents() {
    return (

        <div className="container w-100">
            <div style={{ height: "1000px" }}>
                <div className="contents" style={{ float: 'left', width: "500px" }}>
                    <div style={{ marginTop: "200px" }}>
                        <h1>신뢰 믿음 정직</h1>
                        <p>사기 안치고 싸게 만들겠습니다.</p>
                    </div>
                    <div style={{ marginTop: "300px" }}>
                        <h1>신뢰 믿음 정직</h1>
                        <p>사기 안치고 싸게 만들겠습니다.</p>
                    </div>

                </div>
                <div className="picture" style={{ float: 'right', position: "sticky", top: "200px", width: "50%" }}>
                    <img src={customer} alt="customer" style={{ width: "500px", objectFit: "cover", borderRadius: "20px" }} />
                </div>
            </div>
            <div style={{ height: "1000px", marginTop: "20px" }}>
                <div className="contents" style={{ float: 'right', width: "500px" }}>
                    <div style={{ marginTop: "200px" }}>
                        <h1>신뢰 믿음 정직</h1>
                        <p>사기 안치고 싸게 만들겠습니다.</p>
                    </div>
                    <div style={{ marginTop: "300px" }}>
                        <h1>신뢰 믿음 정직</h1>
                        <p>사기 안치고 싸게 만들겠습니다.</p>
                    </div>

                </div>
                <div className="picture" style={{ float: 'left', position: "sticky", top: "200px", width: "50%" }}>
                    <img src={customer} alt="customer" style={{ width: "500px", objectFit: "cover", borderRadius: "20px" }} />
                </div>
            </div>
        </div>
    )
}

export default Contents