import React from 'react';
import { Map, MapMarker } from 'react-kakao-maps-sdk';
import NavBar from '../NavBar/NavBar';
import location from '../images/location.jpg';
import contact from '../images/contact.jpg';
import Footer from '../Footer/Footer';

function Mapapi() {
    return (
        <div>
            <NavBar />
            <div className='container my-5' style={{ display: "flex", justifyContent: "center" }}>
                <Map
                    center={{ lat: 35.33016170981958, lng: 129.2555913431636 }}
                    style={{ width: "100%", height: "400px" }}
                    level={4} // 지도의 확대 레벨

                >
                    <MapMarker position={{ lat: 35.33016170981958, lng: 129.2555913431636 }}>
                        <div style={{ color: "#000" }}>더샵1급종합모터스</div>
                    </MapMarker>
                </Map>

            </div>
            <div className='container' >
                <img style={{ width: "100%" }} src={location} alt="location" />
            </div>
            <div className="container">
                <img style={{ width: "100%" }} src={contact} alt="location" />
            </div>
            <Footer />
        </div >


    )
}

export default Mapapi