import React from 'react';
import { Carousel } from 'react-bootstrap';
import repair from "../images/repair.jpg";
import factory from "../images/factory.jpg";
import paintlist from "../images/paintlist.jpg";
import title from "../images/title.png";
import maintance from "../images/maintance.jpg";
import subtitle from "../images/subtitle.png";
import painttitle from "../images/painttitle.png"
import paintsubtitle from "../images/paintsubtitle.png"


function Carousel1() {
    return (

        <Carousel>
            <Carousel.Item interval={5000}>
                <img
                    style={{ width: "100%", height: "400px", objectFit: "cover" }}
                    className="d-block w-100"
                    src={factory}
                    alt="First slide"
                />
                <Carousel.Caption>
                    <h1 ><img style={{ width: "500px" }} src={title} alt="title" /></h1>
                    <p><img style={{ width: "400px" }} src={subtitle} alt="subtitle" /></p>
                </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item interval={5000}>
                <img
                    style={{ width: "500px", height: "400px", objectFit: "cover" }}
                    className="d-block w-100"
                    src={repair}
                    alt="30년의 기술력으로 최상의 정비"
                />
                <Carousel.Caption>
                    <h3 style={{ color: "grey", fontWeight: "bold" }}>30년의 기술력으로 최상의 정비</h3>
                    <p style={{ color: "grey" }}>최첨단 장비를 바탕으로한 최신 기술로 최상의 서비스 제공</p>
                </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item interval={5000}>
                <img
                    style={{ width: "500px", height: "400px", objectFit: "cover" }}
                    className="d-block w-100"
                    src={paintlist}
                    alt="Third slide"
                />
                <Carousel.Caption>
                    <h3><img style={{ width: "500px" }} src={painttitle} alt="painttitle" /></h3>
                    <p><img style={{ width: "400px" }} src={paintsubtitle} alt="paintsubtitle" /></p>
                </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item>
                <img
                    style={{ width: "500px", height: "400px", objectFit: "cover" }}
                    className="d-block w-100"
                    src={maintance}
                    alt="Third slide"
                />
                <Carousel.Caption>
                    <h3 style={{ fontWeight: "bold", fontSize: "50px" }}>자동차 정기검사</h3>
                    <p>고객의 안전을 위한 정기검사 및 종합검사 시설 완비</p>
                </Carousel.Caption>
            </Carousel.Item>
        </Carousel>


    )
}

export default Carousel1